@import "./src/common/sass/variables";

.contact {
  padding: 6.5rem 0;
  background-color: $primaryColor;

  .title {
    margin: 0 0 48px 0;
    font-size: 2.5rem;
  }

  .formStyle {
    display: flex;
    justify-content: center;
    .formWrapper {
      display: flex;
      align-items: start;
      flex-direction: column;
      width: 660px;
    }
    textarea {
      width: 260px;
    }
  }

  .button {
    width: 163px;
    height: 54px;
    margin-top: 24px;
    background-color: $bgButtonColor;
    color: $secondTextColor;
    font-weight: 500;
    font-size: 1rem;

    &:hover {
      box-shadow: 0px 5px 15px $bgHoverButtonColor;
    }
  }

}



